.container {
  display: flex;
  flex-direction: column;
  overflow: auto;
  background-color: #222831;
}

.search {
  display: flex;
  width: 50%;
  min-width: 200px;
  margin: 5px auto;
  padding: 5px 0;
  padding-right: 5px;
  border-radius: 30px;
  background-color: white;
}
.icon {
  display: block;
  font-size: 35px;
  padding: 5px;
  border-radius: 50%;
  cursor: pointer;
  transition: 1.2s;
}
.icon:hover {
  background-color: rgba(0, 173, 181, 0.3);
  scale: 1.2;
}

input {
  display: block;
  width: 95%;
  padding: 0 10px;
  border: 1px solid white;
  border-radius: 30px;
  font-size: 20px;
  outline: none;
}

.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(165px, 1fr));
  grid-template-rows: repeat(auto-fit, minmax(220px, 100px));
  grid-auto-flow: dense;
  overflow: auto;
}

@media only screen and (max-width: 720px) {
  .search {
    display: none;
  }
  .icon {
    display: none;
  }
  input {
    display: none;
  }
}
