a {
  text-decoration: none;
}
.card {
  color: white;
  margin: 5px;
  padding: 5px;
  border-radius: 30px;
  transition: 1s;
}
.card:hover {
  background-color: rgba(0, 173, 181, 0.2);
}

img {
  width: 150px;
  height: 150px;
  margin: 0 auto;
}

.cardItems {
  display: flex;
  justify-content: space-between;
}
.info {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
}
.item {
  display: flex;
  flex-direction: column;
  padding-right: 10px;
}
.itemHeader {
  display: flex;
}
.itemFooter {
  display: flex;
  justify-content: space-around;
  font-size: 15px;
  align-items: center;
}
.itemFooter .icon:first-child {
  color: gold;
}
.itemHeader .icon {
  font-size: 22px;
  margin: 3px;
  cursor: pointer;
  transition: 1s;
}
.none {
  display: none;
}

.card h3 {
  margin-bottom: 5px;
  padding-left: 5px;
}

.card p {
  text-align: justify;
}

img {
  width: 150px;
  height: 150px;
  border-radius: 30px;
}
