.container {
  background-color: #222831;
  color: white;
  overflow: auto;
}

.produtItems {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
}
.carusel {
  width: 50%;
  width: 250px;
  height: 265px;
  scroll-snap-type: x mandatory;
  overflow: auto;
  display: flex;
  background-color: white;
  border-radius: 30px;
}
.carusel img {
  scroll-snap-align: start;
  min-width: 250px;
  min-height: 250px;
  padding: 5px;
}

.productInfo {
  width: 50%;
  min-width: 250px;
  text-align: center;
}

.reviews {
  display: flex;
  flex-wrap: nowrap;
  overflow: auto;
}
.review {
  margin-left: 10px;
}
.reviewDate {
  text-align: start;
}
.reviewHeader {
  display: flex;
}
.userName {
  margin-right: 10px;
}
.reviewText {
  text-align: start;
}
