.container {
  background-color: #00adb5;
  padding-top: 5px;
  padding-bottom: 5px;
}
.footerItems a {
  display: none;
}
.footerBtns {
  display: none;
}

@media only screen and (max-width: 720px) {
  .footerItems a {
    display: block;
    font-size: 20px;
    width: 29%;
    min-width: 50px;
    margin: 5px;
    padding: 2px;
    border-radius: 50px;
    cursor: pointer;
    color: #eeeeee;
    background-color: #393e46;
    text-decoration: none;
    transition: 1s all;
  }
  .footerBtns {
    display: block;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 75%;
    min-width: 250px;
    margin: 0 auto;
    border-radius: 50px;
    background-color: #393e46;
    transition: 1.5s all;
  }
  .footerBtns p {
    font-size: 12px;
  }
}
