.container {
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  background-color: #222831;
  color: #eeeeee;
  overflow: auto;
}

.topBox {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.userInfo {
  text-align: start;
  margin-left: 10px;
  margin-bottom: 10px;
}
.itemsInfo {
  display: flex;
  min-width: 200px;
  overflow: auto;
}
.itemInfo {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-around;
  min-width: 150px;
  min-height: 150px;
  margin: 0 10px;
  padding-left: 10px;
  border-radius: 30px;
  background-color: rgba(0, 173, 181, 0.2);
  color: white;
}

.balance {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}
.item {
  display: flex;
}
.item div {
  width: 50%;
  margin: 5px;
  padding: 10px;
  border-radius: 10px;
  background-color: #00adb5;
}
.card {
  max-width: 100%;
  margin: 5px;
  padding: 10px;
  border-radius: 10px;
  background-color: #00adb5;
}

.middleBox {
  display: flex;
  flex-direction: column;
  height: 120px;
}

/* .bottomBox {
} */
.content {
  height: 375px;
  overflow: auto;
}
