.nav {
  background-color: #00adb5;
  padding-top: 5px;
  padding-bottom: 5px;
}

.navItems a {
  font-size: 20px;
  width: 20%;
  min-width: 100px;
  margin: 5px;
  padding: 2px;
  border-radius: 50px;
  cursor: pointer;
  color: #eeeeee;
  background-color: #393e46;
  text-decoration: none;
  transition: 1s all;
}
.navItems a:hover {
  background-color: #222831;
}
.navBtns {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 50%;
  min-width: 200px;
  margin: 0 auto;
  border-radius: 50px;
  background-color: #393e46;
  transition: 1.5s all;
}
.navBtns p {
  font-size: 12px;
}

.search {
  display: none;
}
.icon {
  display: none;
}

input {
  display: none;
}

@media only screen and (max-width: 720px) {
  .navItems a {
    display: none;
  }
  .navBtns {
    display: none;
  }

  .search {
    display: flex;
    width: 50%;
    min-width: 250px;
    margin: 5px auto;
    padding: 5px 0;
    padding-right: 5px;
    border-radius: 30px;
    background-color: white;
  }
  .icon {
    display: block;
    font-size: 35px;
    padding: 5px;
    border-radius: 50%;
    cursor: pointer;
    transition: 1.2s;
  }
  .nav input {
    display: block;
    width: 95%;
    padding: 0 10px;
    border: 1px solid white;
    border-radius: 30px;
    font-size: 20px;
    outline: none;
  }
}
